.canvas {
  position: relative;
  width: 800px;
  height: 600px;
  border: 1px solid black;
}

.canvas .p {
  position: absolute;
  width: 800px;
  height: 600px;
  left: 0;
  top: 0;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.triangles {
  border: 1px solid blueviolet;
  width: 800px;
  height: 600px;
}

.ballz-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.ballz {
  border: none;
  background: #000;
  width: 800px;
  height: 600px;
  position: relative;
}

.paint {
  display: flex;
}

.paint__title {
  font-size: 20px;
  text-align: center;
  line-height: 1.2;
  color: #FFFFFF;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.paint__controls {
  width: 100px;
  background-color: blueviolet;
  padding-top: 10px;
}

.paint canvas {
  display: block;
}
